.menu {
    display: none;
    @media(min-width: 992px) {
        display: block;
        width: 100%;
        height: 80px;
        background-color: #0081B9;
        position: relative;
        border-top: 1px solid #fff;
        border-bottom: 1px solid #fff;
    }
}

.button {
    font-size: 20px;
    font-family: 'CenturyGothic Bold', sans-serif;
    color: #fff;
    padding: 0;
    height: 100%;
    padding-left: 15px;
    padding-right: 15px;
    border-radius: 0;
    @media (min-width: 992px) {
        font-size: 14px;
    }
    @media(min-width: 1400px) {
        font-size: 20px;
    }
}

.button:hover {
    background-color: #EE771C;
    color: #fff !important;
}

.button_container {
    display: flex;
    justify-content: space-between;
    height: 100%;
    align-items: center;
}

.dropdown {
    font-size: 20px;
}

.links {
    color: #244360;
font-family: CenturyGothic;
font-size: 20px;
font-style: normal;
font-weight: 400;
line-height: normal;
padding: 20px 0;
}
