.contacts {
    width: 100%;
    background-color: #EBF6FB;
}

.contacts_content {
    padding: 64px 16px 16px 16px;
}

.contacts_content_header {
    font-family: Bebas Neue Regular;
    font-size: 28px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
}

.contacts_content_info {
    display: flex;
    flex-direction: column;
    margin-top: 15px;
}

.contacts_content_info a {
    margin-bottom: 15px;
}

.contacts_content_info a {
    display: flex;
    flex-direction: row;
    color: #244360;
    text-decoration: none;
    text-align: start;
    font-family: CenturyGothic;
}

.contacts_content_link {
    font-size: 14px;
    margin-left: 8px;
}

.icon {
    width: 18px;
    height: 18px;
}

.map {
    width: 100%;
    aspect-ratio: 1;
}

